.App {
    text-align: center;
}

@media (max-width: 992px) {
    .App {
        width: 100%;
    }
}



@media only screen and (max-width: 575px) {
    .container {
        width: 450px;
    }
}

@media only screen and (max-width: 479px) {
    .container {
        width: 320px;
    }
}
